"use client";

import { useEffect, useRef, useState } from "react";
import { motion, useScroll } from "framer-motion";
import { Menu, X, Home, Folder, User, Mail } from "lucide-react"; // icons



export default function Page() {
  const sectionRef = useRef<HTMLDivElement | null>(null);
  const { scrollYProgress } = useScroll({ target: sectionRef });

  const [isShrunk, setIsShrunk] = useState(false);
  const [isMenuOpen, setIsMenuOpen] = useState(true);

  useEffect(() => {
    return scrollYProgress.on("change", (progress) => {
      if (progress > 0.8) {
        setIsShrunk(true);
      } else {
        setIsShrunk(false);
      }
    });
  }, [scrollYProgress]);

  return (
    <section ref={sectionRef} className="relative">
      {/* Sidebar */}
      <motion.div
        className="navigation-wrap w-hidden-tiny fixed left-0 top-0 h-screen bg-white shadow-lg transition-all duration-500"
        animate={{
          width: isShrunk ? (isMenuOpen ? 80 : 185) : 185,
        }}
      >
        {/* Logo */}
        {!isShrunk && (
          <img
            src="/2-removebg-preview.png"
            alt="logo"
            className="logo-img w-hidden-tiny w-full"
          />
        )}

        {/* Menu Button */}
        <button
          onClick={() => setIsMenuOpen((prev) => !prev)}
          className="menu-button-2 w-nav-button flex items-center justify-center p-2 cursor-pointer"
          aria-label="menu"
        >
          {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
        </button>

        {/* Block Overlay */}
        <motion.div
          className="block-overlay w-hidden-tiny bg-gray-200 transition-all"
          animate={{
            height: isShrunk ? "20vh" : "50vh",
          }}
        />

        {/* Nav */}
        <nav className="side-nav-menu w-nav-menu flex flex-col gap-6 p-4">
          <a href="/" className="side-nav-link flex items-center gap-2">
            <Home size={20} />
            {!isShrunk && "Home"}
          </a>
          <a href="/projects" className="side-nav-link flex items-center gap-2">
            <Folder size={20} />
            {!isShrunk && "My works"}
          </a>
          <a href="#about" className="side-nav-link flex items-center gap-2">
            <User size={20} />
            {!isShrunk && "About"}
          </a>
          <a href="#contact" className="side-nav-link flex items-center gap-2">
            <Mail size={20} />
            {!isShrunk && "Contact"}
          </a>
        </nav>

        {/* Social Links */}
        <SocialLinks />
      </motion.div>

      {/* Hero */}
      <Hero />

      {/* Animated Text */}
      <TextScroll />

      {/* Content */}
      <Content />
    </section>
  );
}

